@import '../../assets/styles/variables';
@import '../../assets/styles/mixins';

.main-discounts {
    @include adaptive-value(padding-bottom, 80, 20, 1);
    
    // .main-discounts__box
    &__box {
        display: flex;
        justify-content: space-between;
        align-items: center;
        @media (max-width: 768px) {
            flex-wrap: wrap;
        }
    }
    // .main-discounts__item
    &__item {
        flex: 0 1 640px;
        &:not(:last-child) {
            margin-right: 20px;
        }
        @media (max-width: 768px) {
            flex: 1 1 100%;
            &:not(:last-child) {
                margin-right: 0;
                margin-bottom: 20px;
            }
        }
    }
}