@import '../../assets/styles/variables';
@import '../../assets/styles/mixins';

.item-discount {
    // .item-discount__link
    &__link {
        display: flex;
        align-items: center;
        padding: 10px 0px;
        min-height: 180px;
        @include adaptive-value(padding-left, 50, 30, 1);
        @include adaptive-value(padding-right, 20, 10, 1);
        background-color: #EDF2F6;
        border-radius: 8px;
        transition: background-color .5s, transform .5s;
        @media (any-hover: hover) {
            &:hover {
                background-color: #bcd5e9;
                transform: scale(1.03);
            }
        }
        &:focus-visible {
            outline: 2px solid #2a5275;
            outline-offset: 2px;
            border-radius: 8px;
            background-color: #bcd5e9;
            transition: background-color .5s, outline 0s;
        }
        @media(max-width: 768px) {
            min-height: 150px;
        }
    }
    // .item-discount__title
    &__title {
        flex: 1 1 auto;
        @include adaptive-font(28, 16, 0);
        font-weight: 700;
        line-height: 120%;
        color: #070C11;
        padding-right: 20px;
    }
    // .item-discount__img
    &__img {
        @include adaptive-value(max-width, 170, 90, 1);
    }
}