@import "../../assets/styles/variables";
@import "../../assets/styles/mixins";

@keyframes sliderPagination {
    from {
        transform: scaleX(0);
    }
    to {
        transform: scaleX(1);
    }
}

.main-slider {
    padding-top: 20px;
    @include adaptive-value("padding-bottom", 80, 20, 1);
    &__slide-link {
    }
    // .main-slider__slide-img
    &__slide-img {
        width: 100%;
        border-radius: 8px;
    }
}

.swiper-pagination {
    padding: 0 15px;
    display: flex;
    .swiper-pagination-bullet {
        flex: 1 1 20%;
        height: 5px;
        background-color: rgba(#c8cacb, 0.2);
        border-radius: 2px;
        opacity: unset;
        transition: all 0.3s;
        &:hover {
            background-color: rgba(#edf2f6, 0.8);
        }
    }
    .swiper-pagination-bullet-active {
        background-color: #edf2f6;
        animation: sliderPagination 5.3s;
        transition: transform 0.3s;
    }
}
